/**
 *
 *
 * @param {String} t 
 * @param {Function} fn 
 * @returns {null} 
 */
const toast = (t, fn) => {
    if (window.toast){
        return
    }

    const tip = document.createElement('div')
    tip.className = 'ui-toast'
    tip.innerHTML = `<p>${ t }</p>`
    document.body.appendChild(tip)
    setTimeout(() => tip.classList.add('active'), 0)
    window.toast = tip

    setTimeout(() => {
        // if (!window.toast){
        //     return
        // }

        tip.classList.remove('active')
        setTimeout(() => {
            try {
                document.body.removeChild(tip)
            }
            catch(e){}
            finally {
                window.toast = null
            }

            fn && fn()
        }, 300)
    }, 1500)
}

export default toast




