const toSex = n => {
    n = Number(n)
    switch(n){
        case 1: 
            n = ''
        break;    
        case 2: 
            n = ''
        break;    
        case 3: 
            n = ''
        break;
        case 0: 
            n = ''
        break;
        default:
        break; 
    }
    return n
}
