/**
 * []
 * html <meta name="viewport" content="width=device-width, user-scalable=no, initial-scale=1, maximum-scale=1, minimum-scale=1, shrink-to-fit=no">
 * px750px1/2
 * reset.cssbodyfont-size: 12px;
 */
/* eslint-disable */
;(function(win){
	var w = 54;			// 
	var h = win.document.documentElement;
	var tid = null;

	function pull(){
		var x = h.clientWidth / 10;
		var dpr = 1;

		if (x > w){
			x = w;
		}
		
		if (/(iphone)|(ipad)/gi.test(win.navigator.appVersion)){
			var r = win.devicePixelRatio;
			if (r >= 3){
				dpr = 3;
			}
			else if (r >= 2){
				dpr = 2;
			}
		}

		h.style.fontSize = x + 'px';
		h.setAttribute('data-dpr', dpr);
	}

	win.addEventListener('resize', function(){
		clearTimeout(tid);
		tid = setTimeout(pull, 300);
	}, false);

	win.addEventListener('pageshow', function(e) {
        if (e.persisted) {
            clearTimeout(tid);
            tid = setTimeout(pull, 300);
        }
    }, false);

	pull();

})(window);



