 /* 
 * @Description: 
 */

import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    // modules: null,
    state: {
        upList: [],
        cMed: null,
        patient: null,
        scheduling: null,
        prescription: null,
        service: null,
        startUrl: null,
        preNo: null,
        userInfo: null,
        pInfo: null,
        grouapsArr: [],
        isUp: false,
        Diagnoses: null,
        Diagnoses2: null,
        reply: null,
        saveFlag: 0,
        pullFlag: 0,
    },
    mutations: {
        'push/upList'(state, upList){
            state.upList = upList
        },
        'push/cMed'(state, cMed){
            state.cMed = cMed
        },
        'push/patient'(state, patient){
            state.patient = patient
        },
        'push/scheduling'(state, scheduling){
            state.scheduling = scheduling
        },
        'push/prescription'(state, prescription){
            state.prescription = prescription
        },
        'push/service'(state, service){
            state.service = service
        },
        'push/startUrl'(state, startUrl){
            state.startUrl = startUrl
        },
        'push/preNo'(state, preNo){
            state.preNo = preNo
        },
        'push/userInfo'(state, userInfo){
            state.userInfo = userInfo
        },
        'push/pInfo'(state, pInfo){
            state.pInfo = pInfo
        },
        'push/isUp'(state, isUp){
            state.isUp = isUp
        },
        'push/Diagnoses'(state, Diagnoses){
            state.Diagnoses = Diagnoses
        },
        'push/Diagnoses2'(state, Diagnoses2){
            state.Diagnoses2 = Diagnoses2
        },
        'push/reply'(state, reply){
            state.reply = reply
        },
        'push/saveFlag'(state, saveFlag){
            state.saveFlag = saveFlag
        },
        'push/pullFlag'(state, pullFlag){
            state.pullFlag = pullFlag
        },
    },
    actions: {
        pushupList({ commit }, upList){
            commit('push/upList', upList)
        },
        pushcMed({ commit }, cMed){
            commit('push/cMed', cMed)
        },
        pushpatient({ commit }, patient){
            commit('push/patient', patient)
        },
        pushscheduling({ commit }, scheduling){
            commit('push/scheduling', scheduling)
        },
        pushprescription({ commit }, prescription){
            commit('push/prescription', prescription)
        },
        pushservice({ commit }, service){
            commit('push/service', service)
        },
        pushstartUrl({ commit }, startUrl){
            commit('push/startUrl', startUrl)
        },
        pushpreNo({ commit }, preNo){
            commit('push/preNo', preNo)
        },
        pushuserInfo({ commit }, userInfo){
            commit('push/userInfo', userInfo)
        },
        pushpInfo({ commit }, pInfo){
            commit('push/pInfo', pInfo)
        },
        pushisUp({ commit }, isUp){
            commit('push/isUp', isUp)
        },
        pushDiagnoses({ commit }, Diagnoses){
            commit('push/Diagnoses', Diagnoses)
        },
        pushDiagnoses2({ commit }, Diagnoses2){
            commit('push/Diagnoses2', Diagnoses2)
        },
        pushreply({ commit }, reply){
            commit('push/reply', reply)
        },
        pushsaveFlag({ commit }, saveFlag){
            commit('push/saveFlag', saveFlag)
        },
        pushpullFlag({ commit }, pullFlag){
            commit('push/pullFlag', pullFlag)
        },
    }
})


